.Landing {
  display: flex;
  flex-direction: column;
  align-items: center;

  height: 100vh;
  box-sizing: border-box;

  padding: 1em;

  .content {
    flex: 0;
    max-width: 50em;
  }

  .icon {
    flex: 1;
    background: none;
    border: none;
    outline: none;

    width: 100%;
    height: auto;
    overflow: hidden;
    object-fit: contain;
    
    &:active {
      #Yolk {
        transform: translateY(10px);
      }
    }
    
    svg {
      animation: breathe 1s ease-in-out infinite alternate-reverse;
    }
  }
}

@keyframes breathe {
  to {
    transform: scale(0.95);
  }
}